
import { defineComponent } from "vue";
import { ContractUser } from "@/contract";

export default defineComponent({
  data() {
    return {
      createDialog: false,
    };
  },
  props: {
    connected: {
      type: Boolean,
    },
    loggedIn: {
      type: Boolean,
    },
    user: {
      type: Object as () => ContractUser,
      default: () => ({} as ContractUser),
    },
  },
  methods: {
    connectWallet() {
      this.$emit("connectWallet");
    },
    createUser() {
      this.$emit("createUser");
    },
  },
});
